@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
.top-menu {
  padding: 1rem 0 2rem;
  font-weight: 600;
  color: #585252;
  box-shadow: 0px 4px 11px 0px #0097ff47; }
  .top-menu__container {
    display: flex;
    justify-content: space-between;
    padding: 0 2rem; }
  .top-menu__links-list {
    display: flex;
    flex-wrap: wrap; }
  .top-menu__link {
    white-space: nowrap;
    padding: 0.3rem;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    border: 2px solid transparent; }
    .top-menu__link--active {
      color: #0097ff;
      border-bottom: 2px solid #0097ff; }

.converter {
  display: flex;
  justify-content: center;
  align-items: center; }
  .converter__title {
    padding: 2rem 0;
    text-align: center; }
  .converter__container {
    padding: 2rem; }
  .converter__from, .converter__to {
    margin: 0.3rem; }

.currencies {
  padding: 2rem 0; }
  .currencies__selections {
    padding-bottom: 1rem; }
  .currencies__list-elem {
    width: 100%;
    padding: 2rem;
    margin: 0.3rem 0;
    display: flex;
    justify-content: space-around;
    box-shadow: 1px 1px 4px -1px rgba(0, 0, 0, 0.33);
    flex-wrap: wrap; }
    .currencies__list-elem--selected {
      padding: 2.5rem;
      background: #bfbfbf86; }
  .currencies__equal {
    width: 15rem;
    display: inline-block;
    text-align: right;
    color: #20bba6; }
  .currencies__text {
    display: inline-block;
    padding: 0.3rem; }

html {
  font-size: 100%; }

html,
body {
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: "Noto Sans", sans-serif;
  font-size: 1.1rem;
  line-height: 1.5;
  color: #111;
  font-weight: 400;
  background-color: #fff; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none; }

img {
  max-width: 100%;
  display: block;
  height: auto; }

.control {
  border: 1px solid #0097ff;
  color: #0097ff;
  outline: 0;
  background: transparent;
  padding: 0.3rem 1rem;
  cursor: pointer;
  display: inline-block;
  height: 45px; }

.container {
  max-width: 1500px;
  margin: 0 auto; }

